/* //Heading */
.Heading {
  background: #ccc;
  text-align: center;
  line-height: 5rem;
}


/* label */

label {
  display: block;
  line-height: 2rem;
  font-size: small;
  text-transform: uppercase;
  font-weight: bold;
}


form {
  border: 2px solid;
  max-width: 500px;
  margin: 2rem auto;
  padding: 1rem;
}

input,
button,
select {
  padding: 0.45rem;
  width: 100%;
  margin-bottom: 0.5rem;
  border: 1px solid;
}

button {
  margin-top: 0.75rem;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
}
button:hover{
  background: #000;
  color: white;
}